import { Component, OnInit, ViewChild } from '@angular/core';
import { AuthService } from 'src/app/services/auth.service';
import { LotteryService } from 'src/app/services/lottery.service';

@Component({
  selector: 'app-buy-ticket-input',
  templateUrl: './buy-ticket-input.component.html',
  styleUrls: ['./buy-ticket-input.component.scss']
})
export class BuyTicketInputComponent implements OnInit {

  showAnimation = false;

  constructor(
    public authService: AuthService,
    public lotteryService: LotteryService
  ) { }

  amount = 0;
  ratio = 1;

  ngOnInit() {
  }

  buyTickets() {
    if(this.amount > 0 && this.amount * .01 <= this.authService.user.amountAvailable) {
      this.lotteryService.buyTicket(this.amount)
        .subscribe(() => {

        });
      this.showAnimation = true;
      setTimeout(() => {
        this.showAnimation = false;
      }, 2000);
    }
  }

  updateAmount(callback) {
    setTimeout(() => {
      if(this.amount === null) {
        return null;
      }

      if (this.amount < .0001) {
        this.amount = 0;
        return;
      }

      if (this.amount) {
        const amountStr = this.amount.toString();
        if (amountStr.indexOf('.') === 0) {
          if (amountStr.length > 5) {
            this.amount = parseFloat(amountStr.substr(0, 5));
          }
        } else {
          const arr = amountStr.split('.');
          if (arr[1] && arr[1].length > 4) {
            arr[1] = arr[1].substr(0, 4);
          }
          this.amount = parseFloat(arr.join('.'));
        }
      }

      callback();
    }, 0);
  }

  checkValue() {
    this.updateAmount(() => {
      if (this.amount > this.authService.user.amountAvailable / .01) {
        this.amount = this.authService.user.amountAvailable / .01;
      }
    });
  }

  setRatio(numerator) {
    const diff = this.lotteryService.currentLotteryInfo.availablePrizePool - this.lotteryService.currentLotteryInfo.currentEntries;
    this.ratio = numerator;
    this.amount = Math.ceil(numerator / 4 *
      ((this.authService.user.amountAvailable / .01) > diff ? diff : (this.authService.user.amountAvailable / .01)));

    if(this.amount < 0) {
      this.amount = 0;
    }
  }

  isWithinRatio(numerator) {
    const diff = this.lotteryService.currentLotteryInfo.availablePrizePool - this.lotteryService.currentLotteryInfo.currentEntries;
    const denoninator = (this.authService.user.amountAvailable / .01) > diff ? diff : (this.authService.user.amountAvailable / .01);

    const percent = this.amount / denoninator;
    if(percent === 0) {
      return false;
    }

    if (numerator === 1 && this.amount !== 0) {
      return true;
    }

    return (numerator / 4) <= percent;
  }

  authorize() {
    this.authService.authorize();
  }
}
