import { WinnerInfo } from "./winner-info";
import { MyEntries } from "./my-entries";

export class PrizePool {
  availablePrizePool: number;
  lotteryStatus: string; // 0 = not started, 1 = pending, 2 = drawing
  countDown: number;
  gameId: string;
  myEntries: MyEntries[];
  allEntries: MyEntries[];
  countDownInterval: any;
  currentEntries: number;
  targetBlockHeight: number;
  openedDate: Date;
  winners: {
    main: WinnerInfo[];
    // first: WinnerInfo;
    // second: WinnerInfo;
    // third: WinnerInfo;
    minipool: {
      isDrawing: boolean;
      winningPreDisplay?: number;
      winningAmount: number;
    };
  };

  constructor(
    prize: number,
    gameId: string,
    lotteryStatus = "0",
    currentEntries = 0,
    myEntries: MyEntries[],
    targetBlockHeight: number
  ) {
    this.openedDate = new Date();
    this.availablePrizePool = prize;
    this.lotteryStatus = lotteryStatus;
    this.countDown = 60;
    this.gameId = gameId;
    this.myEntries = myEntries;
    this.targetBlockHeight = targetBlockHeight;
    this.allEntries = [];
    this.currentEntries = currentEntries;
    this.winners = {
      main: [],
      //   first: new WinnerInfo(),
      //   second: new WinnerInfo(),
      //   third: new WinnerInfo(),
      minipool: {
        isDrawing: true,
        winningAmount: 0,
      },
    };
  }
}
