import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { HomeComponent } from '../home/home.component';
import { LoginComponent } from '../account/login/login.component';
import { RegisterComponent } from '../account/register/register.component';
import { NewAccountOrientationComponent } from '../account/new-account-orientation/new-account-orientation.component';
import { DepositComponent } from '../account/deposit/deposit.component';
import { TwoFactorComponent } from '../account/two-factor/two-factor.component';
import { WithdrawComponent } from '../account/withdraw/withdraw.component';
import { AccountHistoryComponent } from '../account/account-history/account-history.component';
import { PayoutComponent } from '../payout/payout.component';
import { HowToPlayComponent } from '../how-to-play/how-to-play.component';
import { MyEntriesComponent } from '../my-entries/my-entries.component';
import { AuthGuard } from '../account/authguard.guard';
import { InfoComponentComponent } from '../common/info-component/info-component.component';


const routes: Routes = [
  {
    path: 'home',
    component: HomeComponent
  },

  // Auth
  {
    path: 'login',
    component: LoginComponent
  },
  {
    path: 'register',
    component: RegisterComponent
  },
  {
    path: 'orientation',
    component: NewAccountOrientationComponent
  },

  // Account
  {
    path: 'deposit',
    component: DepositComponent,
    canActivate: [AuthGuard]
  },
  {
    path: 'withdraw',
    component: WithdrawComponent,
    canActivate: [AuthGuard]
  },
  {
    path: 'account-history',
    component: AccountHistoryComponent,
    canActivate: [AuthGuard]
  },
  {
    path: '2fa',
    component: TwoFactorComponent,
    canActivate: [AuthGuard]
  },

  {
    path: 'payout-history',
    component: PayoutComponent
  },
  {
    path: 'my-entries',
    component: MyEntriesComponent,
    canActivate: [AuthGuard]
  },
  {
    path: 'how-to-play',
    component: HowToPlayComponent
  },
  {
    path: 'info',
    component: InfoComponentComponent
  },
  {
    path: '',
    redirectTo: '/home',
    pathMatch: 'full'
  }
];

@NgModule({
  imports: [
    RouterModule.forRoot(routes, {useHash: true})
  ],
  exports: [
    RouterModule
  ]
})
export class AppRoutingModule { }
