export class Ticket {
  ticketNumber: string;
  userName: string;
  amount: number;
  portraitUrl?: string;
  isMine?: boolean;

  constructor(
    ticket: string,
    userName: string,
    amount: number,
    isMine: boolean
  ) {
    this.ticketNumber = ticket;
    this.userName = userName;
    this.isMine = isMine;
    this.amount = amount;
  }
}
