import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'toNumber'
})
export class ToNumberPipe implements PipeTransform {

  transform(value: any, ...args: any[]): any {
    if(typeof value === 'string') {
      return parseFloat(value) || 0;
    }

    return value;
  }

}
