import { Component, OnInit, Input } from '@angular/core';
import { Ticket } from 'src/app/models/lottery/ticket';
import { WinnerInfo } from 'src/app/models/lottery/winner-info';
import { Router } from '@angular/router';
import { PlaceMap } from 'src/app/constants/placeMap';

@Component({
  selector: 'app-winner-panel',
  templateUrl: './winner-panel.component.html',
  styleUrls: ['./winner-panel.component.scss']
})
export class WinnerPanelComponent implements OnInit {

  placeMap = PlaceMap;

  constructor(
    public router: Router
  ) {}

  @Input() winnerInfo: WinnerInfo;
  @Input() place: number;

  ngOnInit() {
  }

  goToWinnings() {
    this.router.navigate(['/payout-history'], {
      queryParams: {
        level: this.place + 1
      }
    })
  }

}
