import { Component, OnInit } from '@angular/core';
import { LotteryService } from 'src/app/services/lottery.service';
import { AccountService } from 'src/app/services/account.service';
import { AccountHistory } from 'src/app/models/account/account-history';

@Component({
  selector: 'app-account-history',
  templateUrl: './account-history.component.html',
  styleUrls: ['./account-history.component.scss']
})
export class AccountHistoryComponent implements OnInit {
  typeMap = {
    1: 'buy',
    2: 'withdraw',
    3: 'deposit',
    4: 'win'
  };

  depositHistory: AccountHistory[] = [];
  withdrawHistory: AccountHistory[] = [];

  depositPageNum = 0;
  withdrawPageNum = 0;

  isLoadingDeposit = false;
  isLoadingWithdrawal = false;

  constructor(
    public lotteryService: LotteryService,
    public accountService: AccountService
  ) { }

  ngOnInit() {
    this.accountService.getDepositHistory(
      1,
      20
    ).subscribe((data: any) => {
      if (data.code === 200) {
        this.depositHistory = data.msg;
      }
    });
    this.accountService.getWithdrawalHistory(
      1,
      20
    ).subscribe((data: any) => {
      if (data.code === 200) {
        this.withdrawHistory = data.msg;
      }
    });
  }

  onDepositScroll() {
    this.depositPageNum ++;
    this.accountService.getDepositHistory(
      this.depositPageNum,
      100
    )
      .subscribe((data: any) => {
        const arr = data.msg;

        arr.forEach((item: AccountHistory) => {
          if (!this.depositHistory.find(dup => {
            return dup.id === item.id &&
            dup.uid === item.uid;
          })) {
            this.depositHistory.push(item);
          }
        });
      });
  }

  onWithdrawScroll() {
    this.withdrawPageNum ++;
    this.accountService.getWithdrawalHistory(
      this.withdrawPageNum,
      100
    )
      .subscribe((data: any) => {
        const arr = data.msg;

        arr.forEach((item: AccountHistory) => {
          if (!this.withdrawHistory.find(dup => {
            return dup.id === item.id &&
            dup.uid === item.uid;
          })) {
            this.withdrawHistory.push(item);
          }
        });
      });
  }
}
