import { Component, OnInit } from '@angular/core';
import { AuthService } from 'src/app/services/auth.service';

@Component({
  selector: 'app-withdraw',
  templateUrl: './withdraw.component.html',
  styleUrls: ['./withdraw.component.scss']
})
export class WithdrawComponent implements OnInit {

  isLoading = false;
  showIcon = false;
  address = '';
  amount = 0;
  message = '';

  constructor(
    public authService: AuthService
  ) { }

  ngOnInit() {
  }

  withdraw() {
    if(this.isLoading) {
      return;
    }

    this.isLoading = true;
    this.authService.withdraw(this.amount)
      .subscribe((data: any) => {
        this.isLoading = false;
        if(data.code === 200) {
          this.amount = 0;
          this.message = 'success';
          this.authService.user.amountAvailable = parseFloat(data.msg.balance)
        }
        else {
          this.message = 'an-error-has-occurred';
        }
      });
  }

  updateAmount() {
    setTimeout(() => {
      if(this.amount === null) {
        return null;
      }
      if (this.amount < .0001) {
        this.amount = 0;
        return;
      }
      if (this.amount) {
        const amountStr = this.amount.toString();
        if (amountStr.indexOf('.') === 0) {
          if (amountStr.length > 5) {
            this.amount = parseFloat(amountStr.substr(0, 5));
          }
        } else {
          const arr = amountStr.split('.');
          if (arr[1] && arr[1].length > 4) {
            arr[1] = arr[1].substr(0, 4);
          }
          this.amount = parseFloat(arr.join('.'));
        }
      }
    }, 0);
  }

}
