import { Component, OnInit, Input, ViewChild, ElementRef, AfterViewInit } from '@angular/core';
import { trigger, state, style, transition, animate, group } from '@angular/animations';
import { Subscription } from 'rxjs';
import { BlockChainService } from 'src/app/services/block-chain.service';

@Component({
  selector: 'app-block-chain-info',
  templateUrl: './block-chain-info.component.html',
  styleUrls: ['./block-chain-info.component.scss'],
  animations: [
    [
      trigger('slideInOut', [
        state('in', style({height: '*', opacity: 0})),
        transition(':enter', [
            style({
              height: '0',
              transform: 'translateY(-15px)',
              opacity: 0
            }),
            group([
                animate('250ms ease-in-out', style({transform: 'translateY(0)', height: '*'})),
                animate('300ms ease-in-out', style({'opacity': '1'}))
            ])

        ])
      ])
    ]
  ]
})
export class BlockChainInfoComponent implements OnInit, AfterViewInit {

  constructor(
    public blockChainService: BlockChainService
  ) { }
  @ViewChild('scrollable', {static: false}) scrollable: ElementRef;
  
  getLatestSub = new Subscription();
  getBlockSub = new Subscription();

  openTab(block: string) {
    window.open(`https://etherscan.io/block/${block}`, '_blank')
  }

  ngOnInit() {
    
  }

  parseNumber(string) {
    return parseInt(string, 16);
  }

  parseDate(dateString) {
    return new Date(parseInt(dateString, 16)*1000);
  }

  ngAfterViewInit() {
  }
}
